.library {
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    box-shadow: 2px 2px 50px rgb(204, 204, 204);
    overflow: scroll;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    background: white;
    h2{
        padding: 2rem;
    }
}

.library-song {
    display: flex;
    align-items: center;
    padding: 1rem 2rem 1rem 2rem;
    transition: background 0.5s ease;
    img {
        width: 30%;
    }
    cursor: pointer;
    &:hover {
        background:rgb(203, 195, 227);
    }
}

.song-description {
    padding-left: 1rem;
    h3 {
        font-size: 1rem;
    }
    h4 {
        font-size: 0.7rem;
    }
}

* {
    scrollbar-width: thin;
    scrollbar-color: rgba(82, 78, 183, 0.8) transparent;
}

*::-webkit-scrollbar{
    width: 5px;

}
*::-webkit-scrollbar-track{
    background: transparent;  
}
*::-webkit-scrollbar-thumb{
    background-color: rgba(82, 78, 183, 0.8);
    border-radius: 20px;
    border: transparent;
}

.selected {
    background:#CCCCFF;
}

.active-library{
    transform: translateX(0%);
    opacity: 1;
}

@media screen and (max-width: 768px) {
    .library {
        width: 100%;
    }
}